.container{
    width: 500px;
    height: 700px;
    margin: auto;
    margin-top: 55px;
    border-radius: 15px;
    -webkit-border-radius: 15px;
    -moz-border-radius: 15px;
    -ms-border-radius: 15px;
    -o-border-radius: 15px;
    background-color:  rgb(41, 169, 204);
}
.top-par{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 9px;
    padding-top: 25px;
}
.top-par input{
    /* display: flex;
    justify-content: center; */
    width: 175px;
    height: 0px;
    background: #ebfffc;
    border: none;
    outline: none;
    border-radius: 41px;
    padding: 41px;
    -webkit-border-radius: 41px;
    -moz-border-radius: 41px;
    -ms-border-radius: 41px;
    -o-border-radius: 41px;
    font-size: 20px;
    font-weight: 400;
}
.search-icon{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 50px;
    height: 50px;
    border-radius: 41px;
    background: #ebfffc;
    -webkit-border-radius: 41px;
    -moz-border-radius: 41px;
    -ms-border-radius: 41px;
    -o-border-radius: 41px;
    cursor: pointer;
}
.weather-image{
    margin-top: 11px;
    display: flex;
    justify-content: center;
}

.weather-temp{
    display: flex;
    justify-content: center;
    color: white;
    font-size: 65px;
    font-weight: 400;
}
.weather-location{
    font-size: 60px;
    color: white;
    display: flex;
    justify-content: center;
}
.data-container{
    margin-top: 11px;
    color: white;
    display: flex;
    justify-content: center;
}
.element{
    margin: 10px 50px;
    display: flex;
    align-items: flex-start;
    gap: 9px;
}
.data{
    font-size: 15px;
    font-weight: 500;
}
.text{
    font-weight: 400;
    font-size: 13px;
}